import React, {useState} from 'react';
import './page.css';
import {Slide} from 'react-reveal';
import {HiOutlineMenu} from 'react-icons/hi';
import {IoMdCloseCircle} from 'react-icons/io';
import { Button } from 'react-bootstrap';
import { RiReactjsFill } from 'react-icons/ri';
import IconButton from '@material-ui/core/IconButton';
import Fingerprint from '@material-ui/icons/Fingerprint';

function Header() {
  const [menu, setmenu] = useState(false);
  const showMenu=()=> setmenu(!menu);
  const closeMenu=()=> setmenu(false);

    return (
      <div className='header-container'>
        <div className='header-top'>
        <Fingerprint style={{fontSize: "3rem", fontWeight: "bold", marginLeft: "1rem"}} color="primary"/>
        <div onClick={showMenu}>{menu ? <IoMdCloseCircle className='menu-icon'/> : <HiOutlineMenu className='menu-icon'/>}</div>
        </div>

        <div className='header-right'>
          <Slide top>
          <div className={menu ? 'nav-menu active' : 'nav-menu'}>
            
            <div className='nav-left'>
             <div className='menu-item' onClick={closeMenu}><a href='#'>Home</a></div>
              <div className='menu-item' onClick={closeMenu}><a href='#about'>About</a></div>
              {/* <div className='menu-item' onClick={closeMenu}><a href='#services'>Services</a></div> */}
              <div className='menu-item' onClick={closeMenu}><a href='#projects'>Projects</a></div>
              <div className='menu-item' onClick={closeMenu}><a href='#contact'>Contact</a></div>
            </div>

            <div className='nav-right'>
              <div className='menu-item'  onClick={closeMenu}><a href='#contact'>Sign In</a></div>
              <div className='menu-item'  onClick={closeMenu}><span className='btn-login'>Sign Up</span></div>
            </div>
          </div>
          </Slide>
        </div>
      </div>
    )
}

export default Header;
